.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;

  ol,
  ul {
    padding-left: 30px;
  }
}
div {
  background-image: url("https://1.bp.blogspot.com/-5Se1-7F4-Qs/Xwkxf5igXQI/AAAAAAABaBU/rphe6EGXpZkCVuVd8sT-BmSDdKosKx_pgCNcBGAsYHQ/s1600/food_hanjuku_tamago_tenpura.png");
  background-size: cover;
}

.button1 {
  position: absplute;
  top: 60%;
  laft: 65%;
  border: ipx soid #999;
  lineheight: 100px;
  height: 190px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
